.nurses-container {
  display: flex;
  background-color: #f5f3ef;
  background-image: url("../../Assetss/Capa\ 1.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
}

.nurses-container::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: url("../../Assetss/Capa\ 1.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  z-index: -1;
  display: flex;
  justify-content: center;
}
.left-section1 {
  flex: 1;
  padding: 20px;
}

.logo1 {
  width: 80px;
  height: auto;
  margin-left: 30px;
  margin-top: 10px;
}
.left-section1 h2 {
  margin-left: 30px;
  margin-top: 10px;
  font-weight: 600;
  font-size: 40px;
  font-family: "Poppins", sans-serif;
}
.left-section1 p {
  margin-left: 30px;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  color: #222222;
  font-size: 16px;
  word-spacing: 0.5px;
  letter-spacing: 0.1px;
}
.nurses-container button {
  padding: 11px 60px;
  font-size: 15px;
  background-color: #ae9c7f;
  color: #f3f4f6;
  border: none;
  border-radius: 8px;
  margin-left: 30px;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  margin-bottom: 20px;
}
.right-section1 {
  display: flex;
  align-items: center;
  flex: 1;
}

.image-container1 {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  overflow: hidden;
  background-image: url("../../Assests/Rectangle.png");
  background-size: contain;
  width: 100%;
  height: 80%;
  background-repeat: no-repeat;
  background-position: center;
}

.pay-image1 {
  width: 100%;
  height: auto;
}

.overlay1 {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 32px;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  width: 250px;
  text-align: center;
}

.overlay-text1 {
  position: absolute;
  text-align: center; /* Center the text horizontally */
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 32px;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
}
.additional-image {
  width: 220px;
  margin-top: 230px;
  object-fit: cover;
  margin-left: 160px;
}

.fadeInFromRight {
  animation: fadeInFromRight 400ms ease-out;
  animation-fill-mode: forwards;
}

.fadeOutToBottom {
  animation: fadeOutToBottom 400ms ease-in;
  animation-fill-mode: forwards;
}

.fadeInFromBottom {
  animation: fadeInFromBottom 400ms ease-out;
  animation-fill-mode: forwards;
}

.fadeOutToTop {
  animation: fadeOutToTop 400ms ease-in;
  animation-fill-mode: forwards;
}

.fadeInFromTop {
  animation: fadeInFromTop 400ms ease-out;
  animation-fill-mode: forwards;
}

.fadeOutToLeft {
  animation: fadeOutToLeft 400ms ease-in;
  animation-fill-mode: forwards;
}

.fadeInFromLeft {
  animation: fadeInFromLeft 400ms ease-out;
  animation-fill-mode: forwards;
}

.fadeOutToRight {
  animation: fadeOutToRight 400ms ease-in;
  animation-fill-mode: forwards;
}

@-webkit-keyframes fadeInFromRight {
  0% {
    opacity: 0;
    -webkit-transform: translateX(200px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
  }
}

@keyframes fadeInFromRight {
  0% {
    opacity: 0;
    transform: translateX(200px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

/* fadeOutToBottom keyframes */
@-webkit-keyframes fadeOutToBottom {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(100px);
  }
}

@keyframes fadeOutToBottom {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(100px);
  }
}

@-webkit-keyframes fadeOutToBottom {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(100px);
  }
}

@keyframes fadeOutToRight {
  0% {
    opacity: 1;
    transform: translateX(0);
  }
  100% {
    opacity: 0;
    transform: translateX(200px);
  }
}
/* fadeInFromRight keyframes */
@-webkit-keyframes fadeOutFromRight {
  0% {
    opacity: 1;
    -webkit-transform: translateX(0);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateX(200px);
  }
}

@keyframes fadeInFromBottom {
  0% {
    opacity: 0;
    transform: translateY(100px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* fadeOutToTop keyframes */
@-webkit-keyframes fadeOutToTop {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(-100px);
  }
}

@keyframes fadeOutToTop {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(-100px);
  }
}

/* fadeInFromTop keyframes */
@-webkit-keyframes fadeInFromTop {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-100px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
}

@keyframes fadeInFromTop {
  0% {
    opacity: 0;
    transform: translateY(-100px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* fadeOutToLeft keyframes */
@-webkit-keyframes fadeOutToLeft {
  0% {
    opacity: 1;
    -webkit-transform: translateX(0);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateX(-200px);
  }
}

@keyframes fadeOutToLeft {
  0% {
    opacity: 1;
    transform: translateX(0);
  }
  100% {
    opacity: 0;
    transform: translateX(-200px);
  }
}

/* fadeInFromLeft keyframes */
@-webkit-keyframes fadeInFromLeft {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-200px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
  }
}

@keyframes fadeInFromLeft {
  0% {
    opacity: 0;
    transform: translateX(-200px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

